
.character-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.character-info {
  flex: 2; /* Occupy 2/3 of the row */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.character-name {
  text-align: center;
  font-size: 1.2em;
  font-weight: bold;
  color: #555;
}

.character-details {
  display: flex;
  justify-content: space-around;
  font-size: large;
  font-weight: bold;
  margin-top: -4px;
}


.character-race,
.character-class {
  margin-bottom: 5px;
}
.character-icon {
	flex: 1; /* Occupy 1/3 of the row */
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
  }
  
  .character-icon img {
	max-width: 100%; /* Ensure the image scales to fit the container width */
	max-height: 100%; /* Ensure the image scales to fit the container height */
	width: auto; /* Maintain the aspect ratio */
	height: auto; /* Maintain the aspect ratio */
	object-fit: cover; /* Ensure the image covers the entire container */
	display: block; /* Ensure the image is treated as a block element */
  }
  
  .character-level {
	text-align: center;
	margin-top: 5px; 
	font-size: larger;
  }
.character-card {
	border: 1px solid #ccc;
	border-radius: 8px;
	padding: 16px;
	background-color: #fff;
	transition: transform 0.2s, background-color 0.2s;
	box-sizing: border-box;
	color: #aaa;
	cursor: pointer;
	max-width: 40%;
	transition: transform 0.3s ease, opacity 0.3s ease; /* Add transition for smooth effect */
  }
  
  .character-card-container.assigned {
	opacity: 0.2; /* Slightly lower opacity for assigned characters */
  }


  .character-card:hover {
	transform: scale(1.05);
  }
  
.character-card.hidden {
	display: none;
}
.character-assigned {
	font-size: large;
	font-weight: bold;
	bottom: 40px;
    position: relative;
    height: 0px;
}

  .character-card.selected {
	background-color: #e0f7fa; /* Slightly different background color when selected */
	border-color: #00796b; /* Different border color when selected */
  }
  
