.character-creation-container {
	display: flex;
	height: 100vh; /* Full viewport height */
	overflow: hidden;
	width: 80%;
  
	.accept-character-button {
	margin-top: 20px;
    height: 100px;
    width: 85%;
    left: 10px;
    position: sticky;
    /* margin: 10px; */
    padding: 14px;
	font-size: 1.5em;
    background-color: #81aee3;
    color: #fff;
    border: none;
    border-radius: 5px;
	transition: transform 0.3s ease, background-color 0.3s ease;
    cursor: pointer;
	    .accept-text {
			font-size: 1.2em;
			top: 25%;
			position: relative;
		}
	}

	.accept-character-button:hover {
		background-color: #3f51b5;
		transform: scale(1.05);
	}

	.player-list-container {
		height: 100vh;
		position: relative;
		flex-direction: column;
		display: flex;
		width: 25%;
		max-width: 340px;
	}
  
  .player-list {
	flex: 1;
	display: flex;
	flex-direction: column;
	overflow-y: auto; /* Enable vertical scrolling */
	overflow-x: hidden; /* Disable horizontal scrolling */
	-ms-overflow-style: none;  /* Internet Explorer 10+ */
	scrollbar-width: none;  /* Firefox */
	&::-webkit-scrollbar {
		display: none;  /* Safari and Chrome */
	  }
	padding: 10px;
	.player-card {
		position: relative;
		margin-top: 10px;
		border: 1px solid #ccc;
		border-radius: 8px;
		padding: 16px;
		background-color: #fff;
		transition: transform 0.2s, background-color 0.2s;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		cursor: normal;
		.player-card-body{
			height: unset;
			margin-top: auto; /* Push the body to the bottom */

		}
		.character-row-display {
			display: flex;
			flex-wrap: nowrap;
			gap: 1px;
			margin-top:auto;
			max-height: 74px;
			min-height: 74px;
			border: #a3b8b1;
			background: #bee4d7;
			border-style: outset;
		  }
		.character-card.collapsed {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			
			max-height: 100%; 
			max-width: 25%;
		}
		.character-card.collapsed .character-icon {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			height: 100%;
			min-width: 350%;
		  }
		  
		  .character-card.collapsed .character-icon img {
			
			width: auto; /* Maintain the aspect ratio */
			object-fit: cover; /* Ensure the image covers the entire container */
			padding: 0px;
		  }
		
		  .character-card.collapsed .character-initials {
			font-size: 1.2em;
			font-weight: bold;
		  }

		  .character-card.collapsed:hover {
			opacity: .5;
		  }

	}
	.player-card:hover {
		transform: scale(1.0);
	}
  }
  
  .character-assignment {
	flex: 3;
	display: flex;
	flex-direction: column;
	padding: 10px;
  }
  
  .current-player {
	margin-bottom: 20px;
  }
  
  .next-player-button {
	margin-bottom: 20px;
  }
  
  .character-grid {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	overflow-y: auto; /* Enable vertical scrolling */
	overflow-x: hidden; /* Disable horizontal scrolling */
	-ms-overflow-style: none;  /* Internet Explorer 10+ */
	scrollbar-width: none;  /* Firefox */
	&::-webkit-scrollbar {
		display: none;  /* Safari and Chrome */
	  }
	  transition: transform 0.3s ease, opacity 0.3s ease; /* Add transition for smooth effect */
	}
  
  
  .character-grid .character-card {
	flex: 1 1 calc(50% - 20px); /* Adjust the percentage as needed */
	box-sizing: border-box;
  }

  .overlay {
	position: absolute; /* Position the overlay absolutely */
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 1); /* Semi-opaque background */
	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	font-size: 1.5em;
	font-weight: bold;
	text-align: center;
	border-radius: 8px; /* Match the border radius of the card */
	animation: pulsate 1.5s infinite, shine 2s infinite; /* Pulsating opacity and shiny effect */
	z-index: 1; /* Ensure the overlay is above other elements */
	overflow: hidden; /* Hide overflow for the shiny effect */
	.overlay-text {
		top: 50px;
		position: relative;
	}
  }
  

  @keyframes pulsate {
	0% {
	  opacity: 0.5;
	}
	50% {
	  opacity: 0.7;
	}
	100% {
	  opacity: 0.5;
	}
  }
  
  @keyframes shine {
	0% {
	  background-position: -200%;
	}
	100% {
	  background-position: 200%;
	}
  }
  
  .overlay::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 200%;
	height: 100%;
	background: linear-gradient(
	  120deg,
	  rgba(255, 255, 255, 0.2) 0%,
	  rgba(255, 255, 255, 0.5) 50%,
	  rgba(255, 255, 255, 0.2) 100%
	);
	animation: shine 2s infinite;
  }
}