/* Import the themes.css file */
@import url('../styles/themes.css');

.encounter-display {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 25px;
    border: 2px solid #000000;
    margin: 5px;
    font-size: large;
    color:#bebebe;
    font-weight: bold;
    width: -webkit-fill-available;
    max-width: 50%;
    height: 60px;

    .encounter-name {
        font-size: 1.3em;
        font-weight: bold;
        top: 12px;
        position: relative;
    }
}



.encounter-display.forest {
    background: linear-gradient(0deg, var(--forest-primary-color), var(--forest-secondary-color));
    border-color: var(--forest-tertiary-color);
}

.encounter-display.town { 
    background: linear-gradient(0deg, var(--town-primary-color), var(--town-secondary-color));
    border-color: var(--town-tertiary-color);
}

.encounter-display.mountain {
    background: linear-gradient(180deg, var(--mountain-primary-color), var(--mountain-secondary-color));
    border-color: var(--mountain-tertiary-color);
}

.encounter-display.dungeon {
    background: linear-gradient(0deg, var(--dungeon-primary-color), var(--dungeon-secondary-color));
    border-color: var(--dungeon-tertiary-color);
}

