.campaign-display {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  margin-top: 10px;
  border-style: solid;
  border-color: grey;
 
  border-width: 5px;
  max-width: 100%;
  background-color: green;
  width: -webkit-fill-available;
  transition: transform 0.2s, background-color 0.2s, border-color 0.2s;
  border-radius: 50px;
  padding: 10px 20px;
  justify-content: space-between;
  cursor: pointer;
}
.campaign-display.collapsed {
  flex-direction: row;


  &:hover {
    transform: scale(1.05);
    border-color: rgb(213, 197, 197);
    .campaign-icon img {
      border-color: rgb(153, 234, 192);
    }
  }
  .campaign-icon {
    flex: 0 0 auto;
    margin-right: 20px;
    max-width: 100px;
    max-height: 100%;
    display: relative;

    img {
      max-width: 100%;
      max-height: 100%;
      border-radius: 100%;
      border: 4px solid darkgreen;
    }
  }

  .campaign-info {
    flex: 1 1 auto;
    text-align: center;

    .campaign-name {
      font-weight: bold;
      margin-bottom: 5px;
    }

    .campaign-description {
      font-size: 0.9em;
      color: #fff;
    }
  }

  .campaign-extra {
    flex: 0 0 auto;
    margin-left: 20px;
    text-align: right;
  }
}

.campaign-display.full {
  flex-direction: column;
  .campaign-icon {
    flex: 0 0 auto;
    margin-right: 20px;
    max-width: 100px;
    max-height: 100%;
    display: relative;

    img {
      max-width: 100%;
      max-height: 100%;
      border-radius: 100%;
      border: 4px solid darkgreen;
    }
  }

  .campaign-info {
    flex: 1 1 auto;
    text-align: center;

    .campaign-name {
      font-weight: bold;
      margin-bottom: 5px;
    }

    .campaign-description {
      font-size: 0.9em;
      color: #fff;
    }
  }

  .campaign-extra {
    flex: 0 0 auto;
    margin-left: 20px;
    text-align: right;
  }

}

.campaign-display-header {
  display: flex;
  flex-direction: row;
  align-items: center; // Align items vertically in the center
  width: -webkit-fill-available;

}

.campaign-display-body {
  flex: 1; // Allow the body to grow and take up available space
  min-height: 200px;
  width: -webkit-fill-available;
}

.campaign-display-footer {
  display: flex;
  justify-content: flex-end; // Align buttons to the right
}

