.session-run-container {
	color: orange;
}
.player-card {
	border: 1px solid #ccc;
	border-radius: 8px;
	padding: 16px;
	background-color: #fff;
	transition: transform 0.2s;
	box-sizing: border-box;

	
}
  

  .player-card:hover {
	transform: scale(1.05);
  }

  .player-card.selected {
	color: #81aee3; /* Apply color when selected */
  }

  .player-card-header {
	display: flex;
	align-items: center;
	height:auto;
  }

  .player-card-body {
	margin-top: 16px;
  }

  .player-card-body ul {
	list-style-type: none;
	padding: 0;
  }

  .player-card-body li {
	margin-bottom: 8px;
  }

  .player-card {
	transition: transform 0.3s, color 0.3s; /* Add transition for smooth effect */
	color: grey;
	background-color: #fff;

	&.selected {
	  background-color: #81aee3;
	  color: #fff; /* Apply color when selected */
	}
  
	&:hover {
	  transform: scale(1.15);
	  color: #81aee3; /* Apply color when hovered */
  
	  &.selected {
		color: #fff; /* Apply color when both hovered and selected */
	  }
	}
  }
  
  .player-card-header {
	display: flex;
	gap: 10px;
	max-height: 45%;
  }

  .player-card-body {
	display: flex;
	flex-direction: column;
	gap: 10px;
	height: 55%;
	position: relative;
	max-width: 100%;
	.schedule-display {
		position: absolute;
		bottom: 0px;
		width: 100%;
	}

	.day-square {
		font-size: .9em;
		font-weight: normal;
		height:auto;
		min-width: 5%;
		padding-left: 4px;
		padding-right: 4px;
	  }
	  
	  .day-abbreviation {
		font-size: 1.2em;
		padding: 5px;
	  }
	  .day-percentage {
		display: none;
	  }
  }
  
  
  .player-icon {
	flex: 1; /* Occupy 1/3 of the row */
	display: flex;
	align-items: center;
	justify-content: center;
	max-height: 100%;
	top: 10px;
	position: relative;
  }
  
  .player-icon img {
	max-width: 100%; /* Ensure the image scales to fit the container width */
	max-height: 100px; /* Ensure the image scales to fit the container height */
	width: auto; /* Maintain the aspect ratio */
	height: auto; /* Maintain the aspect ratio */
	object-fit: cover; /* Ensure the image covers the entire container */
	display: block; /* Ensure the image is treated as a block element */
	margin: 5px;
  }
  
  .player-name {
	text-align: center;
	font-size: 1.2em;
	font-weight: bold;
	margin-bottom: 10px; /* Add some space between the name and preferences */
	width: -webkit-fill-available;
  }

  .player-upper-info {
	flex: 2; /* Occupy 2/3 of the row */
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
  }

  .player-card-preferences {
	display: flex;
	flex-direction: column;
	align-items: center;
  }

.player-card-preferences {
	position: relative; /* Ensure the tooltip is positioned relative to this element */
	cursor: pointer; /* Change cursor to indicate hoverable element */
  }
  
  .player-preferences-tooltip {
	visibility: none; /* Hide the tooltip by default */
	display: none;
	background-color: #333;
	color: #fff;
	text-align: center;
	border-radius: 5px;
	padding: 5px;
	position: absolute;
	z-index: 1;
	bottom: 125%; /* Position the tooltip above the element */
	left: 50%;
	transform: translateX(-50%);
	opacity: 0; /* Make the tooltip invisible */
	transition: opacity 0.3s; /* Smooth transition for visibility */
  }
  
  .player-preferences-tooltip::after {
	content: '';
	position: absolute;
	top: 100%; /* Arrow at the bottom of the tooltip */
	left: 50%;
	margin-left: -5px;
	border-width: 5px;
	border-style: solid;
	border-color: #333 transparent transparent transparent; /* Arrow color */
  }
  
  .player-card-preferences:hover .player-preferences-tooltip {
	visibility: visible; /* Show the tooltip on hover */
	opacity: 1; /* Make the tooltip visible */
	display: block;
  }

  .preferences-dots {
	display: flex;
	margin-top: 1px;
	gap: 5px;
  }
  
  .preference-dot {
	width: 15px;
	height: 15px;
	border-radius: 50%;
	background-color: #ccc; /* Default color */
  }

  
.preferences-window {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	padding: 10px;
	border-radius: 15px;
	background-color: #fff;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .preference-pill {
	padding: 5px 10px;
	border-radius: 20px;
	color: #fff;
	font-weight: bold;
	white-space: nowrap;
  }