.player-recruitment-mode {
	position: relative;
	height: 100vh; /* Full viewport height */
	width: 100%;
	overflow: hidden;
  }

  .player-recruitment-title {

	top: 10;
	position: relative;
	//background-color: #13161e; /* Match the background color */
	color: #fff;
	opacity: 1;;
	padding: 10px;
	z-index: 10; /* Ensure it stays above the scrolling content */
  }

  .player-recruitment-status {
	position: relative;
	//background-color: #13161e; /* Match the background color */
	color: #fff;
	font-size: large;
	top: 20px;
  }

  .player-grid-container {
	height: calc(100vh - 50px); /* Adjust based on the height of the title */
	overflow-y: auto; /* Enable vertical scrolling */
	-ms-overflow-style: none;  /* Internet Explorer 10+ */
	scrollbar-width: none;  /* Firefox */
	width: 50%;
	position: absolute;
	right: 5%;
	&::-webkit-scrollbar {
	  display: none;  /* Safari and Chrome */
	}
  }

  .player-status-container {
	height:100%;
	overflow-y: auto; /* Enable vertical scrolling */
	-ms-overflow-style: none;  /* Internet Explorer 10+ */
	scrollbar-width: none;  /* Firefox */
	width: 40%;
	position: absolute;
	left: 5%;
	&::-webkit-scrollbar {
	  display: none;  /* Safari and Chrome */
	}

  }

  .player-status {
	width: 100%;
	height:100%;
	position: relative;
	margin-top: 10px;
	font-size: large;
  }


  .player-status-info-container {
	width: 90%;
	margin: 6%;
	min-height: 65%;
	position: absolute
  }

  .player-status-bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #cccccc;
	z-index: 1; /* Ensure it is behind the player-status element */
	opacity: 0.2;
	border-radius: 8px;
  
  }
  
  .player-grid {
	display: grid;
	grid-template-columns: 50% 50%;
	gap: 25px;
	margin: 5%;
	
  }

  
  .week-display {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 10px;
	margin-bottom: 10px;;
	bottom: 0px;
	position: absolute;
	width: 100%;
  }
  
  .week-display-title {
	font-size: 1.2em;
	margin-bottom: 10px;
  }

  .week-display-days {
	display: flex;
	flex-direction: row;
	width: 100%;
	overflow: hidden;
  }
  
  .week-days {
	display: flex;
	justify-content: space-between;
	width: 100%;
  }
  
  
  .day-square {
	
	width: 150px;
	height: 50px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	color: #fff;
	font-weight: bold;
	border-radius: 5px;
	margin: 5px;
  }
  
  .day-abbreviation {
	font-size: 1.2em;
  }
  
  .day-percentage {
	font-size: 0.8em;
  }

  .player-attitude {
	display: flex;
	justify-content: space-between;
	margin-bottom: 20px;
  }

  .player-attitude-conflicted {
	flex-direction: column;
	align-items: center;
	margin-bottom: 10px;
	position: absolute;
	width: 50%;
	left: 25%;
  }
  
  .attitude-column {
	display: flex;
	flex-direction: column;
	gap: 10px;
	width: 48%;
  }
  
  .attitude-title {
	font-size: 1.2em;
	margin-bottom: 10px;
  }
  
  .attitude-item {
	padding: 5px 10px;
	border-radius: 5px;
	color: #fff;
	font-weight: bold;
	margin-left: 15px;
	margin-right: 15px;
	&.like{
		background-color: green;	
	}
	&.hate {
		background-color: red;
	}
	&.conflicted {
		background-color: grey;
		margin-bottom: 10px;
	}
  }

  .accept-button-container {
	display: flex;
	position: absolute;
	margin-top: 20px;
	bottom: -14%;
	width: 100%;
  }
  
  .accept-button {
	width: 100%;
	padding: 20px;
	height: 20px;
	font-size: 1.5em;
	background-color: #81aee3;
	color: #fff;
	border: none;
	border-radius: 5px;
	cursor: pointer;
  }
  
  .accept-button:hover {
	background-color: #498cd9;
  }

  .accept-button-text {
	text-align: center;
	font-size: large;
	top: -2px;
	position: relative;
  }