.campaign-display-container{
	display: flex;
	width: 100%;
	margin-left: 20px;
}
.campaign-setup-container {
	display: flex;
	height: 100vh; /* Full viewport height */
	
	width: 80%;

	.player-list-container {
		height: 100vh;
		position: relative;
		flex-direction: column;
		display: flex;
		width: 30%;
		margin-top: 16px;
		max-width: 340px;
	}

	.goto-button {
		margin-top: 5px;
		height: 50px;
		width: 85%;
		left: 10px;
		position: sticky;
		/* margin: 10px; */
		padding: 14px;
		font-size: 1em;
		background-color: #81aee3;
		color: #fff;
		border: none;
		border-radius: 5px;
		transition: transform 0.3s ease, background-color 0.3s ease;
		cursor: pointer;
			.accept-text {
				font-size: 1.2em;
				top: 25%;
				position: relative;
			}
		}
	
		.accept-character-button:hover {
			background-color: #3f51b5;
			transform: scale(1.05);
		}
  

	.player-list {
		display: none;
		flex: 1;
		display: flex;
		flex-direction: column;
		overflow-y: auto; /* Enable vertical scrolling */
		overflow-x: hidden; /* Disable horizontal scrolling */
		-ms-overflow-style: none;  /* Internet Explorer 10+ */
		scrollbar-width: none;  /* Firefox */
		&::-webkit-scrollbar {
			display: none;  /* Safari and Chrome */
		  }
		padding: 10px;
		.player-card {
			position: relative;
			margin-top: 10px;
			border: 1px solid #ccc;
			border-radius: 8px;
			padding: 16px;
			background-color: #fff;
			transition: transform 0.2s, background-color 0.2s;
			box-sizing: border-box;
			display: flex;
			flex-direction: column;
			cursor: normal;
			.player-card-body{
				height: unset;
				margin-top: auto; /* Push the body to the bottom */
	
			}
			.character-row-display {
				display: flex;
				flex-wrap: nowrap;
				gap: 1px;
				margin-top:auto;
				max-height: 74px;
				min-height: 74px;
				border: #a3b8b1;
				background: #bee4d7;
				border-style: outset;
			  }
			.character-card.collapsed {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				
				max-height: 100%; 
				max-width: 25%;
			}
			.character-card.collapsed .character-icon {
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				height: 100%;
				min-width: 350%;
			  }
			  
			  .character-card.collapsed .character-icon img {
				
				width: auto; /* Maintain the aspect ratio */
				object-fit: cover; /* Ensure the image covers the entire container */
				padding: 0px;
			  }
			
			  .character-card.collapsed .character-initials {
				font-size: 1.2em;
				font-weight: bold;
			  }
	
			  .character-card.collapsed:hover {
				opacity: .5;
			  }
	
		}
		.player-card:hover {
			transform: scale(.90);
		}
	  }

	.campaign-list-container {
		flex: 3;
		display: flex;
		flex-direction: column;
		padding: 10px;
	}
}